<template>
  <div>
    <section class="search">
      <div class="search-form">
        <form action="#" method="post">
          <div class="form-box">
            <div class="form-group">
              <div class="form-check-inline">
                <label class="form-check-label">
                   
                  <input
                    type="checkbox"
                    v-model="aptdeal"
                    class="form-check-input"
                    name="aptdeal"
                  />
                </label>
              </div>
              <div class="form-check-inline">
                <label class="form-check-label">
                  , 
                  <input
                    type="checkbox"
                    v-model="housedeal"
                    class="form-check-input"
                    name="housedeal"
                  />
                </label>
              </div>
              <div class="form-check-inline">
                <label class="form-check-label">
                   
                  <input
                    type="checkbox"
                    v-model="aptrent"
                    class="form-check-input"
                    name="aptrent"
                  />
                </label>
              </div>
              <div class="form-check-inline">
                <label class="form-check-label">
                  , 
                  <input
                    type="checkbox"
                    v-model="houserent"
                    class="form-check-input"
                    name="houserent"
                  />
                </label>
              </div>
            </div>
            <div class="form-group form-inline sms-form-center">
              <label for="sido" class="mr-sm-2">/</label>
              <select v-model="sido" class="form-control mr-sm-2" name="sido" id="sido">
                <option v-for="sido in sidoList" :key="sido" :value="sido">{{sido}}</option>
              </select>

              <label for="sigungu" class="mr-sm-2">//</label>
              <select v-model="sigungu" class="form-control mr-sm-2" name="sigungu" id="sigungu">
                <option v-for="sigungu in sigunguList" :key="sigungu" :value="sigungu">{{sigungu}}</option>
              </select>

              <label for="dong" class="mr-sm-2"></label>
              <select v-model="dong" class="form-control mr-sm-2" name="dong" id="dong">
                <option v-for="dong in dongList" :key="dong" :value="dong">{{dong}}</option>
              </select>

              <label for="aptName" class="mr-sm-2"> :</label>
              <input
                v-model="aptName"
                type="text"
                class="form-control mr-sm-2"
                name="aptName"
                placeholder="  "
              />
              <button @click.prevent="search" class="btn btn-primary"></button>
              <span v-if="this.$store.state.name!=''">
              <button @click.prevent="addRegion" class="btn btn-secondary ml-2"> </button>
              </span>
            </div>
          </div>
        </form>
      </div>
    </section>
    <section class="container">
      <div id="house-map"></div>
      <div class="table-responsive">
        <table class="table table-hover table-bordered">
          <thead>
            <tr id="head-row">
              <th @click="clickSort('address');" title="">
                <i class="fas fa-angle-down"></i>
              </th>
              <th @click="clickSort('aptName');" title=" ">
                <i class="fas fa-angle-down"></i> 
              </th>
              <th @click="clickSort('buildYear');" title=" ">
                <i class="fas fa-angle-down"></i> 
              </th>
              <th @click="clickSort('area');" title="">
                <i class="fas fa-angle-down"></i>
              </th>
              <th @click="clickSort('floor');" title="">
                <i class="fas fa-angle-down"></i>
              </th>
              <th @click="clickSort('dealDate');" title="">
                <i class="fas fa-angle-down"></i>
              </th>
              <th @click="clickSort('dealAmount');" title=" ()">
                <i class="fas fa-angle-down"></i> ()
              </th>
              <th @click="clickSort('rentMoney');" title="">
                <i class="fas fa-angle-down"></i>
              </th>
              <th @click="clickSort('type');" title=" ">
                <i class="fas fa-angle-down"></i> 
              </th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="deal in tableList" v-bind:key="deal.no" @click="goDetail(deal.no)">
              <td :title="deal.address">{{deal.address}}</td>
              <td :title="deal.aptName">{{deal.aptName}}</td>
              <td :title="deal.buildYear">{{deal.buildYear}}</td>
              <td :title="deal.area">{{deal.area}}</td>
              <td :title="deal.floor">{{deal.floor}}</td>
              <td :title="deal.dealDate">{{deal.dealDate}}</td>
              <td :title="deal.dealAmount">{{deal.dealAmount}}</td>
              <td :title="deal.rentMoney">{{deal.rentMoney}}</td>
              <td :title="deal.typeString">{{deal.typeString}}</td>
            </tr>
          </tbody>
        </table>
      </div>

      <!--    -->
      <nav id="house-page" v-html="nav"></nav>
    </section>
  </div>
</template>

<script>
import http from "@/http-common.js";
import $ from "jquery";

export default {
  data() {
    return {
      aptdeal: true,
      housedeal: true,
      aptrent: true,
      houserent: true,

      sido: "",
      sigungu: "",
      dong: "",
      aptName: "",

      sidoList: [],
      sigunguList: [],
      dongList: [],

      mapList: [],
      tableList: [],
      list: [],
      nav: "",
      nod: 0,
      listCreated: false,

      map: {},
      mapCreated: false,
      mapCreate: 0
    };
  },
  computed: {
    id() {
      return this.$store.state.id;
    }
  },
  watch: {
    mapCreate() {
      console.log("init map in mapCreate watcher called!");
      this.initMap();
      this.mapCreated = true;
    },
    sido() {
      this.setGugunName();
    },
    sigungu() {
      this.setDongName();
    },
    dong() {
      if (!this.listCreated) {
        this.listCreated = true;
        this.search();
      }
    },
    aptdeal() {
      this.search();
    },
    housedeal() {
      this.search();
    },
    aptrent() {
      this.search();
    },
    houserent() {
      this.search();
    }
  },
  // Created :    
  created() {
    console.log("created called!!");

    //     /  
    http
      .get("/rest/house/sidoname")
      .then(response => {
        this.sidoList = response.data;
        this.sido = this.sidoList[0];
      })
      .catch(error => {
        alert("Error: " + error);
      });
  },
  // Updated :      
  updated() {
    let vue = this;
    $("#house-page a").on("click", function(e) {
      let n = parseInt($(this).attr("id"));
      e.preventDefault();

      http
        .post("/rest/house/pagenav", {
          pg: n,
          nod: vue.nod,
          list: vue.list
        })
        .then(response => {
          vue.tableList = response.data.tableList;
          vue.nav = response.data.nav;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    });
  },
  // Mounted :   API   
  mounted() {
    if (window.kakao && window.kakao.maps) {
      if (!this.mapCreated && this.mapList.length > 0) {
        console.log("initMap in mounted is Called!");
        this.initMap();
        this.mapCreated = true;
      }
    } else {
      /* global kakao */
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=c0153be2e7c8003d17fd11e5d2f1dcfe&libraries=services";
      document.head.appendChild(script);
    }
  },
  methods: {
    initMap() {
      //    
      var container = document.getElementById("house-map");
      //   
      var options = { level: 3 };
      if (this.mapList.length > 0) {
        options.center = new kakao.maps.LatLng(
          this.mapList[0].lat,
          this.mapList[0].lng
        );
      }

      //    .
      var map = new kakao.maps.Map(container, options);
      console.log("Add map on dom complete");

      //           
      var mapTypeControl = new kakao.maps.MapTypeControl();

      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

      //          
      var zoomControl = new kakao.maps.ZoomControl();
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
      console.log("Add control on map complete");

      this.map = map;

      let positions = [];

      //      LatLngBounds  
      //   Marker    
      var bounds = new kakao.maps.LatLngBounds();

      for (let deal of this.mapList) {
        let obj = {};
        obj.latlng = new kakao.maps.LatLng(deal.lat, deal.lng);
        obj.content =
          '<div class="kakao-wrap">' +
          '    <input id="kakao-no" type="hidden" value=' +
          deal.no +
          "/>" +
          '    <div class="kakao-info">' +
          '        <div class="kakao-title">' +
          deal.aptName +
          "        </div>" +
          '        <div class="kakao-body">' +
          '            <div class="kakao-desc">' +
          '                <div class="kakao-ellipsis">' +
          deal.address +
          "</div>" +
          '                <div class="kakao-ellipsis kakao-red">   </div>' +
          "            </div>" +
          "        </div>" +
          "    </div>" +
          "</div>";
        obj.trickVal = deal.no;

        positions.push(obj);
        bounds.extend(obj.latlng);
      }

      //        
      map.setBounds(bounds);

      for (let i = 0; i < positions.length; i++) {
        //  
        let marker = new kakao.maps.Marker({
          map: map, //   
          position: positions[i].latlng //  
        });

        //    
        var infowindow = new kakao.maps.InfoWindow({
          content: positions[i].content //   
        });
        infowindow.trickVal = positions[i].trickVal;

        //  mouseover  mouseout  
        //     
        // for        
        kakao.maps.event.addListener(
          marker,
          "mouseover",
          this.makeOverListener(map, marker, infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "mouseout",
          this.makeOutListener(infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "click",
          this.makeClickListener(infowindow)
        );
      }
      console.log("Add all marker complete");
    },
    makeOverListener(map, marker, infowindow) {
      return function() {
        infowindow.open(map, marker);
      };
    },
    makeOutListener(infowindow) {
      return function() {
        infowindow.close();
      };
    },
    makeClickListener(infowindow) {
      let vue = this;
      return function() {
        console.log(infowindow.trickVal);
        vue.goDetail(infowindow.trickVal);
      };
    },
    goDetail(no) {
      console.log(no);
      this.$router.push("/house/detail/" + no);
    },
    clickSort(by) {
      console.log(by);
      http
        .post("/rest/house/sort", {
          by: by,
          nod: this.nod,
          list: this.list
        })
        .then(response => {
          this.list = response.data.list;
          this.tableList = response.data.tableList;
          this.nav = response.data.nav;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    addRegion() {
      if(this.id.length == 0) {
        alert('      .');
        this.$router.push('/user/login');
        return;
      }

      let sigungu = this.sido + " " + this.sigungu + " " + this.dong;

      http
        .post("/rest/interest", {
          sigungu: sigungu,
          userid: this.id
        })
        .then(response => {
          if (response.data) {
            alert(" ");
            this.$router.push('/interest/list');
          } else {
            alert("  ");
          }
        })
        .catch(error => {
          if(error == "Error: Network Error")
            alert("  .");
          else
            alert(error);
        });
    },
    search() {
      console.log("search() called!!");

      //     !
      if (
        !this.aptdeal &&
        !this.housedeal &&
        !this.aptrent &&
        !this.houserent
      ) {
        alert("   .");
        return;
      }

      if (this.sido != "") {
        alert("    !");
        return;
      }

      let addr = this.sido + " " + this.sigungu + " " + this.dong;

      http
        .post("/rest/house/list", {
          aptdeal: this.aptdeal,
          housedeal: this.housedeal,
          aptrent: this.aptrent,
          houserent: this.houserent,

          address: addr,
          aptName: this.aptName
        })
        .then(response => {
          this.list = response.data.list;
          this.tableList = response.data.tableList;
          this.mapList = response.data.mapList;
          this.nav = response.data.nav;
          this.nod = response.data.nod;

          //  watch     !
          this.mapCreate++;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    setGugunName() {
      http
        .get("/rest/house/gugunname/" + this.sido)
        .then(response => {
          this.sigunguList = response.data;
          this.sigungu = this.sigunguList[0];
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    setDongName() {
      http
        .get("/rest/house/dongname/" + this.sigungu)
        .then(response => {
          this.dongList = response.data;
          this.dong = this.dongList[0];
        })
        .catch(error => {
          alert("Error: " + error);
        });
    }
  }
};
</script>
<style scoped>
.search-form {
  text-align: center;
  padding: 10px;
}

.sms-form-center {
  justify-content: center;
}

.table {
  margin-top: 20px;
}

th {
  background: skyblue;
  cursor: pointer;
}

th,
td {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

th > i {
  visibility: hidden;
}

.pagination a {
  display: inline-block;
  cursor: pointer;
}

.nav {
  display: inline-block;
}

#nav-container {
  text-align: center;
}

#house-map {
  height: 600px;
}
</style>