<template>
  <div id="root-template-box">
    <div class="mt-3 row">
      <!--   -->
      <!-- <button class="btn btn-danger ml-5" @click="grepInterestRegion">   </button> -->
      <div id="sidebox" class="interest-table">
        <div class="table-responsive">
          <table class="table table-hover table-bordered">
            <thead>
              <tr>
                <th colspan="4"> </th>
              </tr>
            </thead>
            <tbody>
              <template v-if="regions.length > 0">
                <template v-for="(region, index) in regions">
                  <tr :key="region.no">
                    <td>
                      <div style="float : left">{{region.sigungu}}</div>
                      <div style="text-align : right">
                        <a href="#" @click.prevent="searchEnv(index)">
                          
                          <i class="fa fa-search" aria-hidden="true"></i>
                        </a>
                        &nbsp;
                        &nbsp;
                        &nbsp;
                        <a
                          href="#" style="margin-right : 4px;"
                          @click.prevent="searchShop(index)"
                        >
                          
                          <i class="fa fa-search" aria-hidden="true"></i>
                        </a>
                        &nbsp;
                        &nbsp;
                        &nbsp;
                        <a
                          href="#"
                          @click.prevent="removeRegion(index)"
                        >
                          <i class="fa fa-trash" aria-hidden="true"></i>
                        </a>
                      </div>
                    </td>
                  </tr>
                </template>
              </template>
              <template v-else>
                <tr>
                  <td>  .</td>
                </tr>
              </template>
            </tbody>
          </table>
        </div>
      </div>

      <!-- ,  -->
      <div class="information-table mt-3 offset-3 col-9">
        <template v-show="envs.length > 0 || shops.length > 0">
          <div class="container">
          <!--      -->
          <h3>{{sigudong}} {{infotype}}</h3>
          <!--   -->
          <div v-show="shops.length > 0" class="category">
            <ul>
              <li id="foodMenu" @click="changeMarker('food')">
                <span class="ico_comm ico_food"></span>
                
              </li>
              <li id="lifeMenu" @click="changeMarker('life')">
                <span class="ico_comm ico_life"></span>
                
              </li>
              <li id="shopMenu" @click="changeMarker('shop')">
                <span class="ico_comm ico_shop"></span>
                
              </li>
              <li id="studyMenu" @click="changeMarker('study')">
                <span class="ico_comm ico_study"></span>
                /
              </li>
              <li id="houseMenu" @click="changeMarker('house')">
                <span class="ico_comm ico_house"></span>
                
              </li>
              <li id="playMenu" @click="changeMarker('play')">
                <span class="ico_comm ico_play"></span>
                //
              </li>
              <li id="sleepMenu" @click="changeMarker('sleep')">
                <span class="ico_comm ico_sleep"></span>
                
              </li>
            </ul>
          </div>

          <!--   -->
          <div v-show="envs.length > 0" class="env-category">
            <ul>
              <li id="groundMenu" @click="changeEnvMarker('ground')">
                <span class="ico_comm ico_ground"></span>
                
              </li>
              <li id="waterMenu" @click="changeEnvMarker('water')">
                <span class="ico_comm ico_water"></span>
                
              </li>
              <li id="poisonMenu" @click="changeEnvMarker('poison')">
                <span class="ico_comm ico_poison"></span>
                
              </li>
              <li id="trashMenu" @click="changeEnvMarker('trash')">
                <span class="ico_comm ico_trash"></span>
                
              </li>
              <li id="noiseMenu" @click="changeEnvMarker('noise')">
                <span class="ico_comm ico_noise"></span>
                
              </li>
            </ul>
          </div>
          </div>
          <!--  -->
          <div class="container map-box">
            <div id="map"></div>
          </div>


          <!-- ,   (    ) -->
          <div v-if="envs.length > 0 || shops.length > 0" class="container table-box">
            <div class="table-responsive info-table">
              <table class="table table-hover table-bordered">
                <!--     -->
                <template v-if="envs.length > 0">
                  <thead>
                    <tr>
                      <th @click="sortEnv('name')"> </th>
                      <th @click="sortEnv('type')"></th>
                      <th @click="sortEnv('address')"></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="env in envs"
                      v-bind:key="env.no"
                      :class="{ground: env.type == '',
                                 water: env.type == '',
                                 poison: env.type == '',
                                 trash: env.type == '',
                                 noise: env.type == ''}"
                    >
                      <td>{{env.name}}</td>
                      <td>{{env.type}}</td>
                      <td>{{env.address}}</td>
                    </tr>
                  </tbody>
                </template>

                <!--     -->
                <template v-if="shops.length > 0">
                  <thead>
                    <tr>
                      <th @click="sortShop('name')"> </th>
                      <th @click="sortShop('type4')"> </th>
                      <th @click="sortShop('address')"></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="(shop, index) in shops"
                      v-bind:key="shop.no"
                      @click="clickShop(index)"
                      :class="{food: shop.type1 == '',
                               life: shop.type1 == '',
                               shop: shop.type1 == '',
                               study: shop.type1 == '/',
                               house: shop.type1 == '',
                               play: shop.type1 == '//',
                               sleep: shop.type1 == ''}"
                    >
                      <td :title="shop.name">{{shop.name}}</td>
                      <td :title="shop.type4">{{shop.type4}}</td>
                      <td :title="shop.address">{{shop.address}}</td>
                    </tr>
                  </tbody>
                </template>
              </table>
            </div>
          </div>
          <div v-show="envs.length > 0 || shops.length > 0" class="container chart-box">
            <canvas id="chart"></canvas>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/http-common.js";
import Chart from "chart.js";
import $ from "jquery";

export default {
  data() {
    return {
      sigudong: '',
      infotype: '',

      regions: [],
      shops: [],
      envs: [],

      curIdx: -1,

      mapList: [],
      map: {},
      mapCreated: false,
      mapCreate: 0,

      shopMarkers: [],
      shopSetMarkers: [],

      envMarkers: [],
      envSetMarkers: [],

      isUpdated: false
    };
  },
  watch: {
    mapCreate() {
      console.log("init map in mapCreate watcher called!");
      this.initMap();
      this.mapCreated = true;
    }
  },
  computed: {
    id() {
      return this.$store.state.id;
    }
  },
  created() {
    this.getRegionList();
  },
  // Mounted :   API   
  mounted() {
    if (window.kakao && window.kakao.maps) {
      if (!this.mapCreated && this.mapList.length > 0) {
        console.log("initMap in mounted is Called!");
        this.initMap();
        this.mapCreated = true;
      }
    } else {
      /* global kakao */
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=c0153be2e7c8003d17fd11e5d2f1dcfe&libraries=services";
      document.head.appendChild(script);
    }
  },
  // scroll   
  updated() {
    let currentPosition = parseInt($("#sidebox").css("top"));

    $(window).scroll(function() {
      let position = $(window).scrollTop();
      $("#sidebox")
        .stop()
        .animate(
          {
            top: position + currentPosition + "px"
          },
          1000
        );
    });

    if(this.shops.length > 0) {
      this.shopCategorySet();
    } else if(this.envs.length > 0) {
      this.envCategorySet();
    }

    this.isUpdated = true;
  },
  methods: {
    shopCategorySet() {
      var foodMenu = document.getElementById("foodMenu");
      var lifeMenu = document.getElementById("lifeMenu");
      var shopMenu = document.getElementById("shopMenu");
      var studyMenu = document.getElementById("studyMenu");
      var houseMenu = document.getElementById("houseMenu");
      var playMenu = document.getElementById("playMenu");
      var sleepMenu = document.getElementById("sleepMenu");

      let menus = [
        foodMenu,
        lifeMenu,
        shopMenu,
        studyMenu,
        houseMenu,
        playMenu,
        sleepMenu
      ];
      let trs = [
        $(".food"),
        $(".life"),
        $(".shop"),
        $(".study"),
        $(".house"),
        $(".play"),
        $(".sleep")
      ];

      for(let i = 0; i < 7; i++) {
        if(menus[i].className == "menu_selected") {
          trs[i].show();
        } else {
          trs[i].hide();
        }
      }
    },
    envCategorySet() {
      var groundMenu = document.getElementById("groundMenu");
      var waterMenu = document.getElementById("waterMenu");
      var poisonMenu = document.getElementById("poisonMenu");
      var trashMenu = document.getElementById("trashMenu");
      var noiseMenu = document.getElementById("noiseMenu");

      let menus = [groundMenu, waterMenu, poisonMenu, trashMenu, noiseMenu];
      let trs = [
        $(".ground"),
        $(".water"),
        $(".poison"),
        $(".trash"),
        $(".noise")
      ];

      for(let i = 0; i < 5; i++) {
        if(menus[i].className == "menu_selected") {
          trs[i].show();
        } else {
          trs[i].hide();
        }
      }
    },
    searchEnv(index) {
      this.isUpdated = false;
      this.curIdx = index;
      this.sigudong = this.regions[index].sigungu;
      this.infotype = ' ';
      console.log("searchEnv: " + this.regions[index].sigungu);
      http
        .post("/rest/env", {
          sigudong: this.regions[index].sigungu
        })
        .then(response => {
          this.envs = response.data.list;
          this.shops = [];

          //  
          this.mapList = response.data.map;
          this.mapCreate++;

          //  
          this.makeChart(response.data.chart, "env");
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    searchShop(index) {
      this.isUpdated = false;
      this.curIdx = index;
      this.sigudong = this.regions[index].sigungu;
      this.infotype = ' ';
      console.log("searchShop: " + this.regions[index].sigungu);
      http
        .post("/rest/shop", {
          sigudong: this.regions[index].sigungu
        })
        .then(response => {
          this.shops = response.data.list;
          this.envs = [];

          //  
          this.mapList = this.shops;
          this.mapCreate++;

          // chart 
          this.makeChart(response.data.chart, "shop");
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    makeChart(chartData, gubun) {
      // ,  
      let labels = [];
      let data = [];
      for (let d of chartData) {
        labels.push(d.type);
        data.push(d.count);
      }

      let label = "";
      if (gubun == "shop") {
        label = " ";
      } else {
        label = "  ";
      }

      let ctx = $("#chart");
      new Chart(ctx, {
        type: "radar",
        data: {
          labels: labels,
          datasets: [
            {
              data: data,
              label: label,
              backgroundColor: "#ccebff",
              borderColor: "#0099ff",
              pointBackgroundColor: "#003d66"
            }
          ]
        }
      });
    },
    clickShop(index) {
      console.log(index);
    },
    sortShop(by) {
      http
        .post("/rest/shop/sort", {
          list: this.shops,
          by: by
        })
        .then(response => {
          this.shops = response.data;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    sortEnv(by) {
      http
        .post("/rest/env/sort", {
          list: this.envs,
          by: by
        })
        .then(response => {
          this.envs = response.data;
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    removeRegion(index) {
      if (index == this.curIdx) {
        this.shops = [];
        this.envs = [];
        this.mapList = [];

        $("#map").hide();
      }
      http
        .post("/rest/interest/delete", {
          sigungu: this.regions[index].sigungu,
          userid: this.regions[index].userid
        })
        .then(response => {
          if (response.data) {
            alert(" ");
          } else {
            alert("  ");
          }
          this.getRegionList();
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    getRegionList() {
      http
        .get("/rest/interest/" + this.id)
        .then(response => {
          this.regions = response.data;

          if(this.envs.length == 0 &&
             this.shops.length == 0 &&
             this.regions.length > 0) {
            
            this.searchShop(0);
          }
        })
        .catch(error => {
          alert("Error: " + error);
        });
    },
    initMap() {
      if (this.mapList.length == 0) return;

      if (this.shops.length > 0) {
        this.initMapForShop();
      } else {
        this.initMapForEnv();
      }
    },
    initMapForShop() {
      //   
      $("#map").show();

      //    
      var container = document.getElementById("map");
      //   
      var options = { level: 3 };
      if (this.mapList.length > 0) {
        options.center = new kakao.maps.LatLng(
          this.mapList[0].lat,
          this.mapList[0].lng
        );
      }

      //    .
      var map = new kakao.maps.Map(container, options);
      console.log("Add map on dom complete");

      //           
      var mapTypeControl = new kakao.maps.MapTypeControl();

      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

      //          
      var zoomControl = new kakao.maps.ZoomControl();
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
      console.log("Add control on map complete");

      this.map = map;

      //       
      let positions = [];

      //       
      this.shopMarkers = [];
      for (let i = 0; i < 7; i++) {
        this.shopMarkers.push([]);

        positions.push([]);
      }

      var bounds = new kakao.maps.LatLngBounds();
      for (let shop of this.mapList) {
        let obj = {};
        obj.latlng = new kakao.maps.LatLng(shop.lat, shop.lng);
        obj.content =
          '<div class="kakao-wrap">' +
          '    <input id="kakao-no" type="hidden" value=' +
          shop.no +
          "/>" +
          '    <div class="kakao-info">' +
          '        <div class="kakao-title">' +
          shop.name +
          "        </div>" +
          '        <div class="kakao-body">' +
          '            <div class="kakao-desc">' +
          '                <div class="kakao-ellipsis">' +
          shop.address +
          "</div>" +
          "            </div>" +
          "        </div>" +
          "    </div>" +
          "</div>";
        obj.trickVal = shop.no;

        //      
        if (shop.type1 == "") {
          positions[0].push(obj);
        } else if (shop.type1 == "") {
          positions[1].push(obj);
        } else if (shop.type1 == "") {
          positions[2].push(obj);
        } else if (shop.type1 == "/") {
          positions[3].push(obj);
        } else if (shop.type1 == "") {
          positions[4].push(obj);
        } else if (shop.type1 == "//") {
          positions[5].push(obj);
        } else if (shop.type1 == "") {
          positions[6].push(obj);
        }

        //       
        bounds.extend(obj.latlng);
      }
      map.setBounds(bounds);

      var markerImageSrc = "http://127.0.0.1:8080/img/shop_category.png";

      //  Marker    
      //  , ,      
      function createMarkerImage(src, size, options) {
        var markerImage = new kakao.maps.MarkerImage(src, size, options);
        return markerImage;
      }

      //       
      let vue = this;
      function createMarker(position, image) {
        //  
        var marker = new kakao.maps.Marker({
          position: position.latlng,
          image: image
        });

        //    
        var infowindow = new kakao.maps.InfoWindow({
          content: position.content //   
        });
        infowindow.trickVal = position.trickVal;

        //  mouseover  mouseout  
        //     
        // for        
        kakao.maps.event.addListener(
          marker,
          "mouseover",
          vue.makeOverListener(map, marker, infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "mouseout",
          vue.makeOutListener(infowindow)
        );
        kakao.maps.event.addListener(
          marker,
          "click",
          vue.makeClickListener(infowindow)
        );

        return marker;
      }

      // Narker  
      let point_h = 0;
      for (let seo_i = 0; seo_i < 7; seo_i++) {
        let poss = positions[seo_i];
        for (var i = 0; i < poss.length; i++) {
          var imageSize = new kakao.maps.Size(22, 26),
            imageOptions = {
              spriteOrigin: new kakao.maps.Point(10, point_h),
              spriteSize: new kakao.maps.Size(36, 250)
            };

          //   
          var markerImage = createMarkerImage(
              markerImageSrc,
              imageSize,
              imageOptions
            ),
            marker = createMarker(poss[i], markerImage);

          //     
          this.shopMarkers[seo_i].push(marker);
        }
        point_h += 36;
      }

      //  food   
      this.changeMarker("food");
      console.log("Add all marker complete");
    },
    initMapForEnv() {
      console.log("Map for env");
      $("#map").show();

      //    
      var container = document.getElementById("map");
      //   
      var options = { level: 4 };
      if (this.mapList.length > 0) {
        options.center = new kakao.maps.LatLng(
          this.mapList[0].lat,
          this.mapList[0].lng
        );
      }

      //    .
      var map = new kakao.maps.Map(container, options);
      console.log("Add map on dom complete");

      //           
      var mapTypeControl = new kakao.maps.MapTypeControl();

      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

      //          
      var zoomControl = new kakao.maps.ZoomControl();
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);
      console.log("Add control on map complete");

      this.map = map;

      //       
      let positions = [];

      //       
      this.envMarkers = [];
      for (let i = 0; i < 5; i++) {
        this.envMarkers.push([]);

        positions.push([]);
      }

      var bounds = new kakao.maps.LatLngBounds();
      for (let env of this.mapList) {
        let obj = {};
        obj.latlng = new kakao.maps.LatLng(env.lat, env.lng);

        //      
        if (env.type == "") {
          positions[0].push(obj);
        } else if (env.type == "") {
          positions[1].push(obj);
        } else if (env.type == "") {
          positions[2].push(obj);
        } else if (env.type == "") {
          positions[3].push(obj);
        } else if (env.type == "") {
          positions[4].push(obj);
        }

        //       
        bounds.extend(obj.latlng);
      }
      map.setBounds(bounds);

      var markerImageSrc = "http://127.0.0.1:8080/img/shop_category.png";

      //  Marker    
      //  , ,      
      function createMarkerImage(src, size, options) {
        var markerImage = new kakao.maps.MarkerImage(src, size, options);
        return markerImage;
      }

      //       
      function createMarker(position, image) {
        //  
        var marker = new kakao.maps.Marker({
          position: position.latlng,
          image: image
        });

        return marker;
      }

      // Marker  
      let point_h = 0;
      for (let seo_i = 0; seo_i < 5; seo_i++) {
        let poss = positions[seo_i];
        for (var i = 0; i < poss.length; i++) {
          var imageSize = new kakao.maps.Size(22, 26),
            imageOptions = {
              spriteOrigin: new kakao.maps.Point(10, point_h),
              spriteSize: new kakao.maps.Size(36, 250)
            };

          //   
          var markerImage = createMarkerImage(
              markerImageSrc,
              imageSize,
              imageOptions
            ),
            marker = createMarker(poss[i], markerImage);

          //     
          this.envMarkers[seo_i].push(marker);
        }
        point_h += 36;
      }

      //  ground   
      this.changeEnvMarker("ground");
    },
    makeOverListener(map, marker, infowindow) {
      return function() {
        infowindow.open(map, marker);
      };
    },
    makeOutListener(infowindow) {
      return function() {
        infowindow.close();
      };
    },
    makeClickListener(infowindow) {
      return function() {
        console.log(infowindow.trickVal);
      };
    },
    setMarkers(map, idx) {
      let markers = this.shopMarkers[idx];
      for (var i = 0; i < markers.length; i++) {
        markers[i].setMap(map);
      }
    },
    setEnvMarkers(map, idx) {
      let markers = this.envMarkers[idx];
      for (var i = 0; i < markers.length; i++) {
        markers[i].setMap(map);
      }
    },
    changeMarker(type) {
      var foodMenu = document.getElementById("foodMenu");
      var lifeMenu = document.getElementById("lifeMenu");
      var shopMenu = document.getElementById("shopMenu");
      var studyMenu = document.getElementById("studyMenu");
      var houseMenu = document.getElementById("houseMenu");
      var playMenu = document.getElementById("playMenu");
      var sleepMenu = document.getElementById("sleepMenu");

      let menus = [
        foodMenu,
        lifeMenu,
        shopMenu,
        studyMenu,
        houseMenu,
        playMenu,
        sleepMenu
      ];

      let targ = 0;
      if (type == "food") targ = 0;
      else if (type == "life") targ = 1;
      else if (type == "shop") targ = 2;
      else if (type == "study") targ = 3;
      else if (type == "house") targ = 4;
      else if (type == "play") targ = 5;
      else if (type == "sleep") targ = 6;

      if (menus[targ].className == "menu_selected") {
        menus[targ].className = "";
        this.setMarkers(null, targ);
      } else {
        menus[targ].className = "menu_selected";
        this.setMarkers(this.map, targ);
      }

      if(this.isUpdated) {
        this.shopCategorySet();
      }
    },
    changeEnvMarker(type) {
      var groundMenu = document.getElementById("groundMenu");
      var waterMenu = document.getElementById("waterMenu");
      var poisonMenu = document.getElementById("poisonMenu");
      var trashMenu = document.getElementById("trashMenu");
      var noiseMenu = document.getElementById("noiseMenu");

      let menus = [groundMenu, waterMenu, poisonMenu, trashMenu, noiseMenu];

      let targ = 0;
      if (type == "ground") targ = 0;
      else if (type == "water") targ = 1;
      else if (type == "poison") targ = 2;
      else if (type == "trash") targ = 3;
      else if (type == "noise") targ = 4;

      if (menus[targ].className == "menu_selected") {
        menus[targ].className = "";
        this.setEnvMarkers(null, targ);
      } else {
        menus[targ].className = "menu_selected";
        this.setEnvMarkers(this.map, targ);
      }

      if(this.isUpdated) {
        this.envCategorySet();
      }
    },
    grepInterestRegion() {
      $("#sidebox").css("top", 433);
    }
  }
};
</script>

<style scoped>
#adddiv {
  text-align: right;
}
#page {
  text-align: center;
}
.info-table {
  height: 500px;
  border: 1px solid black;
}

th,
td {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

