<template>
  <div>
    <div class="container mt-3">
      <h3> </h3>

      <form action="#">
        <!-- / -->
        <div class="form-group form-inline">
          <label for="province" class="col-sm-3 offset-3 control-label">/</label>
          <div class>
            <input
              v-model="province"
              ref="province"
              type="text"
              class="form-control"
              name="province"
            />
          </div>
        </div>

        <!-- // -->
        <div class="form-group form-inline">
          <label for="city" class="col-sm-3 offset-3 control-label">//</label>
          <div class>
            <input v-model="city" ref="city" type="text" class="form-control" name="city" />
          </div>
        </div>

        <!-- / -->
        <div class="form-group form-inline">
          <label for="dong" class="col-sm-3 offset-3 control-label">//</label>
          <div class>
            <input v-model="dong" ref="dong" type="text" class="form-control" name="dong" />
          </div>
        </div>

        <div class="form-group" id="adddiv">
          <button @click.prevent="addRegion" class="btn button pr-2 pl-2" id="add"></button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import http from "@/http-common.js";

export default {
  data() {
    return {
      province: "",
      city: "",
      dong: ""
    };
  },
  computed: {
    id() {
      return this.$store.state.id;
    }
  },
  methods: {
    addRegion() {
      if (this.province.length == 0) {
        alert("/  ");
        this.$refs.province.focus();
        return;
      }
      if (this.city.length == 0) {
        alert("//  ");
        this.$refs.city.focus();
        return;
      }
      if (this.dong.length == 0) {
        alert("//   ");
        this.$refs.dong.focus();
        return;
      }

      let sigungu = this.province + ' ' + 
                    this.city + ' ' + 
                    this.dong;

      http
        .post("/rest/interest", {
          sigungu: sigungu
          , userid: this.id
        })
        .then(response => {
          if (response.data) {
            alert(" ");
          } else {
            alert("  ");
          }

          this.$router.push("/interest/list");
        })
        .catch(error => {
          alert("Error: " + error);
          this.$router.push("/interest/list");
        });
    }
  }
};
</script>

<style scoped>
.button {
  border: none;
  color: #fff;
  padding: 15px 0;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 15px;
  margin: 4px;
  cursor: pointer;
}

#add {
  background-color: #8fc242; /**/
  text-align: center;
}
#adddiv {
  text-align: center;
}

h3 {
  text-align: center;
}
</style>