<template>
  <div class="container-fluid">
    <br />
    <div class="row">
      <div class="map-news-box col-4 container right-box">
        <div class="maps">
          <div id="map-container">
            <div id="rvWrapper">
              <div id="roadview" style="width:100%;height:100%;"></div>
              <!--   div  -->
            </div>
            <div id="mapWrapper">
              <div id="map" style="width:100%;height:100%"></div>
              <!--   div  -->
              <div id="roadviewControl" @click="setRoadviewRoad"></div>
            </div>
          </div>
        </div>
        <button class="btn btn-primary mt-3" @click="reposition">   </button>
      </div>

      <!--   -->
      <div class="container content-box col-3 align-self-center">
        <div class="info-box table-responsive">
          <table class="table">
            <thead>
              <tr>
                <td colspan="2">
                  <h3>
                    <i class="fas fa-building"></i>   
                  </h3>
                </td>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th></th>
                <td :title="house.address">{{house.address}}</td>
              </tr>
              <tr>
                <th> </th>
                <td :title="house.aptName">{{house.aptName}}</td>
              </tr>
              <tr>
                <th> </th>
                <td :title="house.buildYear">{{house.buildYear}}</td>
              </tr>
              <tr>
                <th></th>
                <td :title="house.area">{{house.area}} ({{this.pyung}})</td>
              </tr>
              <tr>
                <th></th>
                <td :title="house.floor">{{house.floor}}</td>
              </tr>
              <tr>
                <th> </th>
                <td :title="house.typeString">{{house.typeString}}</td>
              </tr>
              <tr>
                <th> </th>
                <td :title="house.dealAmount">{{house.dealAmount}} ( : )</td>
              </tr>
              <tr v-if="house.type > 2">
                <th></th>
                <td :title="house.rentMoney">{{house.rentMoney}} ( : ) </td>
              </tr>
              <tr>
                <th> </th>
                <td :title="house.dealDate">{{house.dealDate}}</td>
              </tr>
            </tbody>
          </table>

          <router-link class="btn btn-info" to="/"> </router-link>
        </div>
      </div>

      <!--  -->
      <div class="container chart-box col-4">
        <div v-if="this.avgDeal>-1" class="chart-container">
          <canvas class="chart-canvas row" id="chart-area"></canvas>
          <div style="font-size:14px">
            <span>  : {{this.avgDeal}} ( : )</span>
          </div>
        </div>
        <div v-if="this.avgRent1>-1" class="chart-container">
          <canvas class="chart-canvas row" id="chart-area-rent"></canvas>
          <div style="font-size:14px">
            <p>
               / : {{this.avgRent1}} ( : )
              <br />
                : {{this.avgRent2}} ( : )
            </p>
          </div>
        </div>
      </div>
    </div>
    <hr />
    <!-- , -->
    <div class="row right-box mt-3 bottom-content">
      <div class="col-7" style="padding-right : 5%">
        <div id="news">
          <div><strong>  </strong></div>
          <br />
          <div v-for="(n,index) in this.news" :key="n.no">
            <div v-if="index<3" style="float : left; font-size : 14px;">
              <a :title="n.title" v-bind:href="n.link" target="_blank">{{n.title}}</a>
              <div :title="n.description">{{n.description}}</div>
              <div class="pubDate">
                <span class="pubDate">{{n.pubDate}}</span>
              </div>
            </div>
            <br />
          </div>
        </div>
      </div>
      <div class="chart-container col-4">
        <canvas class="chart-canvas" id="crime-chart"></canvas>
      </div>
      <div class="col-1" />
    </div>
  </div>
</template>

<script>
import http from "@/http-common.js";
import Chart from "chart.js";
import $ from "jquery";
import Vue from "vue";
import VueCarousel from "vue-carousel";
Vue.use(VueCarousel);

export default {
  data() {
    return {
      no: 0,
      house: {},
      pyung : '',

      news: [],

      nos: [],
      avgDeal: "",
      avgRent1: "",
      avgRent2: "",

      map: {},
      currentPos: {},
      address: "",
      mapCreated: false,

      overlayOn: false,
      marker: {},
      container: {},
      rv: {},
      rvClient: {}
    };
  },
  created() {
    console.log("Detail created() called!");
    this.no = this.$route.params.no;
    http
      .get("/rest/house/detail/" + this.no)
      .then(response => {
        this.house = response.data.house;
        this.news = response.data.news;
        this.pyung = (this.house.area*0.3025).toFixed(2);

        // address watcher   (  )
        this.address = this.house.address;

        // house    chart    
        http
          .post("/rest/house/chart", {
            address: this.house.address,
            aptName: this.house.aptName,
            type: this.house.type
          })
          .then(response => {
            this.nos = response.data.nos;
            this.makeChart(response.data);
            this.avgDeal = response.data.avgDeal;
            this.avgRent1 = response.data.avgRent1;
            this.avgRent2 = response.data.avgRent2;
          })
          .catch(error => {
            alert("Error: " + error);
          });

        http
          .post("/rest/crime", {
            address: this.house.address
          })
          .then(response => {
            this.crimeChart(response.data);
          })
          .catch(error => {
            alert("Error: " + error);
          });
      })
      .catch(error => {
        alert("Error: ", error);
      });
  },
  mounted() {
    if (window.kakao && window.kakao.maps) {
      if (this.address.length > 0) {
        console.log("initMap in mounted is Called!");
        this.initMap();
        this.mapCreated = true;
      }
    } else {
      /* global kakao */
      const script = document.createElement("script");
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=c0153be2e7c8003d17fd11e5d2f1dcfe&libraries=services";
      document.head.appendChild(script);
    }
  },
  watch: {
    address() {
      if (!this.mapCreated) {
        console.log("init map in address watcher called!");
        this.initMap();
      }
    }
  },
  methods: {
    initMap() {
      //  DB     
      this.setMap(new kakao.maps.LatLng(this.house.lat, this.house.lng));
    },
    setMap(coords) {
      this.overlayOn = false; //         
      this.container = document.getElementById("map-container"); //     div 

      var mapContainer = document.getElementById("map"), //   div 
      rvContainer = document.getElementById("roadview"); //  div 

      this.currentPos = coords;

      let mapCenter = coords;

      //   
      var options = {
        //   
        center: coords,
        //   / 
        level: 3
      };

      //    .
      var map = new kakao.maps.Map(mapContainer, options);
      this.map = map;

      //   
      var rv = new kakao.maps.Roadview(rvContainer);
      this.rv = rv;

      //    ID     
      var rvClient = new kakao.maps.RoadviewClient();
      this.rvClient = rvClient;

      let vue = this;

      //   
      var markImage = new kakao.maps.MarkerImage(
        "https://t1.daumcdn.net/localimg/localimages/07/2018/pc/roadview_minimap_wk_2018.png",
        new kakao.maps.Size(26, 46),
        {
          //   .
          //     
          spriteSize: new kakao.maps.Size(1666, 168),
          //      .
          // background-position    .
          spriteOrigin: new kakao.maps.Point(705, 114),
          offset: new kakao.maps.Point(13, 46)
        }
      );

      //    
      var marker = new kakao.maps.Marker({
        image: markImage,
        position: mapCenter,
        draggable: true
      });

      this.marker = marker;

      //       
      kakao.maps.event.addListener(rv, "position_changed", function() {
        //     
        var rvPosition = rv.getPosition();

        //      
        vue.map.setCenter(rvPosition);

        //       
        if (vue.overlayOn) {
          //      
          vue.marker.setPosition(rvPosition);
        }
      });

      //  dragend  
      kakao.maps.event.addListener(marker, "dragend", function() {
        //     
        var position = vue.marker.getPosition();

        //      
        vue.toggleRoadview(position);
      });

      //   
      kakao.maps.event.addListener(map, "click", function(mouseEvent) {
        //          
        if (!vue.overlayOn) {
          return;
        }

        //   
        var position = mouseEvent.latLng;

        //    
        vue.marker.setPosition(position);

        //     
        vue.toggleRoadview(position);
      });

      //           
      var mapTypeControl = new kakao.maps.MapTypeControl();

      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

      //  
      new kakao.maps.Marker({
        map: map, //   
        position: this.currentPos //  
      });

      this.map = map;
    },
    toggleRoadview(position) {
      let vue = this;
      this.rvClient.getNearestPanoId(position, 50, function(panoId) {
        //  ID null   
        if (panoId === null) {
          vue.toggleMapWrapper(true, position);
        } else {
          vue.toggleMapWrapper(false, position);

          // panoId  
          vue.rv.setPanoId(panoId, position);
        }
      });
    },
    toggleMapWrapper(active, position) {
      if (active) {
        //    div  100%  class 
        this.container.className = "";

        //     relayout  
        this.map.relayout();

        //       (position) 
        this.map.setCenter(position);
      } else {
        //       50%  class 
        //    
        if (this.container.className.indexOf("view_roadview") === -1) {
          this.container.className = "view_roadview";

          //     relayout  
          this.map.relayout();

          //       (position) 
          this.map.setCenter(position);
        }
      }
    },
    toggleOverlay(active) {
      if (active) {
        this.overlayOn = true;

        //      
        this.map.addOverlayMapTypeId(kakao.maps.MapTypeId.ROADVIEW);

        //    
        this.marker.setMap(this.map);

        //     
        this.marker.setPosition(this.map.getCenter());

        //     
        this.toggleRoadview(this.map.getCenter());
      } else {
        this.overlayOn = false;

        //      
        this.map.removeOverlayMapTypeId(kakao.maps.MapTypeId.ROADVIEW);

        //    
        this.marker.setMap(null);
      }
    },
    setRoadviewRoad() {
      var control = document.getElementById("roadviewControl");

      //    
      if (control.className.indexOf("active") === -1) {
        control.className = "active";

        //     
        this.toggleOverlay(true);
      } else {
        control.className = "";

        //    
        this.closeRoadview();
      }
    },
    closeRoadview() {
      var position = this.marker.getPosition();
      this.toggleMapWrapper(true, position);
      //    
      this.toggleOverlay(false);
    },

    reposition() {
      console.dir(this.map);
      this.map.panTo(this.currentPos);
    },
    makeChart(data) {
      //     
      let dmin = 999999999;
      let dmax = 0;
      for (let d of data.data) {
        if (d < dmin) dmin = d;
        if (d > dmax) dmax = d;
      }

      dmin -= 2000;
      dmax += 2000;

      if (dmin < 0) dmin = 0;

      let ctx = $("#chart-area");
      let chartObject = new Chart(ctx, {
        type: "line",
        data: {
          labels: data.labels,
          datasets: [
            {
              data: data.data,
              label: " ",
              backgroundColor: "#ccebff",
              borderColor: "#0099ff",
              pointBackgroundColor: "#003d66"
            }
          ]
        },
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  min: dmin,
                  max: dmax
                }
              }
            ]
          }
        }
      });
      ctx.click(e => {
        let activePoints = chartObject.getElementsAtEvent(e);
        if (activePoints.length > 0) {
          let clickedElementIndex = activePoints[0]._index;

          let no = this.nos[clickedElementIndex];

          this.resetDetail(no);
        }
      });

      dmax = 0; //  max  
      for (let d of data.deposit) {
        if (d > dmax) dmax = d;
      }
      dmax += 5000;

      dmin = 0; //  max  
      for (let r of data.rent) {
        if (r > dmin) dmin = r;
      }
      if (dmin == 0) dmin = dmax;
      else dmin += 40;

      // /       
      let ctx_rent = $("#chart-area-rent");
      let chartObject_rent = new Chart(ctx_rent, {
        type: "line",
        data: {
          labels: data.monthly,
          datasets: [
            {
              label: "",
              fill: false,
              borderColor: "rgb(255, 130, 0)",
              data: data.rent,
              yAxisID: ""
            },
            {
              data: data.deposit,
              label: "",
              fill: false,
              borderColor: "rgb(255, 210, 0)",
              yAxisID: ""
            }
          ]
        },
        options: {
          scales: {
            yAxes: [
              {
                id: "",
                type: "linear",
                position: "left",
                ticks: {
                  max: dmin,
                  min: 0
                }
              },
              {
                id: "",
                type: "linear",
                position: "right",
                ticks: {
                  max: dmax,
                  min: 0
                }
              }
            ]
          }
        }
      });
      ctx_rent.click(e => {
        let activePoints = chartObject_rent.getElementsAtEvent(e);
        if (activePoints.length > 0) {
          let clickedElementIndex = activePoints[0]._index;
          let no = this.nos[clickedElementIndex];
          this.resetDetail(no);
        }
      });
    },
    //   
    crimeChart(data) {
      let ctx = $("#crime-chart");
      new Chart(ctx, {
        type: "bar",
        data: {
          labels: data.labels,
          datasets: [
            {
              data: data.data,
              label: "     (2018)",
              backgroundColor: "#ff1a66"
            }
          ]
        },
        options: {
          responsive: true,
          tooltips: {
            enabled: false
          },
          hover: {
            animationDuration: 0
          },
          animation: {
            onComplete: function() {
              var chartInstance = this.chart,
                ctx = chartInstance.ctx;
              ctx.fillStyle = "black";
              ctx.textAlign = "center";
              ctx.textBaseline = "bottom";

              this.data.datasets.forEach(function(dataset, i) {
                var meta = chartInstance.controller.getDatasetMeta(i);
                meta.data.forEach(function(bar, index) {
                  var data = dataset.data[index];
                  ctx.fillText(data, bar._model.x, bar._model.y - 5);
                });
              });
            }
          }
        }
      });
    },
    resetDetail(no) {
      this.no = no;
      http
        .get("/rest/house/detail/" + no)
        .then(response => {
          this.house = response.data.house;
          this.pyung = (this.house.area*0.3025).toFixed(2);
        })
        .catch(error => {
          alert("Error: ", error);
        });
    }
  }
};
</script>

<style scoped>
.content-box {
  margin-top: 0px;
  text-align: center;
}
.info-box {
  width: 400px;
  margin: auto;
  margin-bottom: 20px;
}
th,
td {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
li {
  list-style: none;
}
#description #title {
  font-size: 13px;
}
.pubDate {
  font-size: 12px;
  text-align: right;
}
.chart-box {
  text-align: center;
}
.chart-canvas {
  margin-top: 20px;
}
.chart-container {
  overflow: auto;
  width: 90%;
}
.right-box {
  padding-left: 2%;
}
.maps {
  width: 600px;
  height: 300px;
}
#news {
  max-height: 300px;
  text-overflow: ellipsis;
  overflow: auto;
}

.news-item > div {
  margin-top: 5px;
}

.bottom-content {
  height: 300px;
}

#map-container {
  overflow: hidden;
  height: 600px;
  position: relative;
}
#mapWrapper {
  width: 100%;
  height: 600px;
  z-index: 1;
}
#rvWrapper {
  width: 100%;
  height: 600px;
  top: 0;
  right: 0;
  position: absolute;
  z-index: 0;
}
#map-container.view_roadview #mapWrapper {
  width: 0%;
}
#roadviewControl {
  position: absolute;
  top: 5px;
  left: 5px;
  width: 42px;
  height: 42px;
  z-index: 1;
  cursor: pointer;
  background: url(https://t1.daumcdn.net/localimg/localimages/07/2018/pc/common/img_search.png)
    0 -450px no-repeat;
}
#roadviewControl.active {
  background-position: 0 -350px;
}

</style>